// BestCustomers.tsx
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { UserStar } from 'lucide-react';

const customers = [
    { id: 1, name: "Amit Kumar", stamps: 18, visits: 12 },
    { id: 2, name: "Priya Sharma", stamps: 15, visits: 10 },
    { id: 3, name: "Rahul Verma", stamps: 13, visits: 9 }
]



export function BestCustomers() {
    return (
        <Card className="card">
            <CardHeader>
                <CardTitle className="text-xl font-semibold text-primary flex gap-2 items-center">
                    <UserStar className='text-primary size-5' />
                    Best Customers
                </CardTitle>
            </CardHeader>

            <CardContent className="space-y-4">
                {customers.slice(0, 3).map((c, index) => (
                    <div
                        key={c.id}
                        className="flex items-center justify-between rounded-lg border p-3 hover:bg-muted/40 transition"
                    >
                        <div className="flex items-center gap-3">
                            <Avatar className="h-10 w-10">
                                <AvatarFallback>
                                    {c.name?.charAt(0) || "C"}
                                </AvatarFallback>
                            </Avatar>

                            <div>
                                <p className="font-medium">{index + 1}. {c.name}</p>
                                <p className="text-sm text-muted-foreground">
                                    {c.stamps} stamps · {c.visits} visits
                                </p>
                            </div>
                        </div>
                    </div>
                ))}
            </CardContent>
        </Card>
    )
}
